body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px 15px;
}

.search-form button:hover {
  background-color: #0056b3;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: 300px;
  text-align: center;
  background-color: #fff;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  text-align: left;
  padding: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

